body {
  margin: 0;
  line-height: 1.4;
  background-color: #15101B;
  font-family: "Acrom";
  font-size: 16px;
  line-height: 1.5;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

a {
  color: #fff;
  text-decoration: none;
  font-weight: 400;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0 0 10px;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
}

.page {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.center {
  text-align: center;
  margin: 0 auto;
}

/* Header */
.header {
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #3B3741;
  position: relative;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
}

.header__logo {
  width: 139px;
  height: 29px;
  margin-right: 40px;
}

.header__search {
  max-width: 350px;
  width: 100%;
}

.header__btn {
  display: flex;
}

.header__icon {
  display: flex;
  align-items: center;
  position: relative;
}
.header__icon-item {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 16px;
}
.header__icon-item:last-child {
  margin-right: 0;
}
.header__icon .col {
  position: relative;
  bottom: -5px;
  left: -3px;
  width: 32px;
  height: 32px;
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
}

/* Search */
.search {
  width: 100%;
  position: relative;
}

.search__input {
  color: #fff;
  font-weight: 400;
  width: 100%;
  padding: 11px 20px;
  outline: none;
  max-width: 350px;
  position: relative;
  background: #1D1625;
  border: 1px solid #3B3741;
  box-sizing: border-box;
  border-radius: 220px;
  font-family: "Acrom";
  font-size: 18px;
  transition: border-color 0.2s linear;
}
.search__input:focus {
  border-color: rgb(216, 106, 224);
}

.search__photo {
  position: absolute;
  right: 27px;
  top: 50%;
  transform: translateY(-50%);
}

.search__input::-moz-placeholder {
  font-size: 16px;
  line-height: 19px;
  color: #9E9EA7;
}

.search__input:-ms-input-placeholder {
  font-size: 16px;
  line-height: 19px;
  color: #9E9EA7;
}

.search__input::placeholder {
  font-size: 16px;
  line-height: 19px;
  color: #9E9EA7;
}

/* Btn */
.btn {
  display: inline-block;
  padding: 10px 19px;
  min-width: 130px;
  text-decoration: none;
  position: relative;
  font-family: "Acrom";
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  cursor: pointer;
  background: none;
  border: none;
  text-decoration: none;
  background: #15101B;
  background-clip: padding-box;
  border: solid 2px transparent;
  border-radius: 220px;
}
.btn:before {
  position: absolute;
  content: "";
  background: linear-gradient(to right, rgb(148, 187, 254), rgb(217, 107, 224), rgb(154, 34, 247));
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: -1;
  margin: -2px;
  border-radius: inherit;
}
.btn span {
  background-image: linear-gradient(to right, rgb(191, 137, 235), rgb(216, 106, 224), rgb(194, 80, 232));
  color: transparent;
  -webkit-background-clip: text;
  z-index: 3;
  position: relative;
}
.btn:after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgb(216, 106, 224);
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transition: opacity 0.2s linear;
  border-radius: 220px;
}
.btn:hover:after {
  opacity: 0.2;
}

/* Notification */
.notification__modal {
  background-color: #15101B;
  position: absolute;
  top: 79px;
  right: 320px;
  z-index: 11111111;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  transition: height 0.2s linear;
  opacity: 0;
  border: 2px solid #D566E1;
  box-shadow: 0px 0px 37px rgba(113, 22, 155, 0.61);
  transition: opacity 0.2s linear;
  overflow: hidden;
  padding-bottom: 15px;
}
.notification__modal-title {
  text-align: center;
}

.block {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.nonee {
  display: none;
}

.notification-active {
  opacity: 1;
}

.notifications-btn {
  width: 160px;
  background: linear-gradient(90deg, #94BBFE 0%, #AC7DFE 52.08%, #EF1AF8 100%);
  border-radius: 220px;
  padding: 10px 53px;
  margin: 0 auto;
  margin-top: 15px;
  display: block;
}

.notification__modal-title {
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  margin: 17px 10px 15px;
}

.notification__item {
  display: flex;
  align-items: center;
