}
.notification__item-photo {
  width: 42px;
  height: 42px;
  margin-right: 13px;
  position: relative;
}
.notification__item-photo .notification-user {
  border-radius: 50%;
  width: 42px;
  height: 42px;
}
.notification__item-photo .notification-tick {
  position: absolute;
  right: 0;
  bottom: 0;
}
.notification__item-name {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}
.notification__item-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #ADADAD;
  word-wrap: break-word;
}

.notification__item-content {
