  flex-direction: column;
}

.modal__inner {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 570px;
  width: 100%;
  margin-top: 16px;
}
.modal__inner-item {
  width: 84px;
  height: 84px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #221D27;
  margin: 0 5px;
  border-radius: 10px;
  margin-bottom: 10px;
  border: 1px solid transparent;
  transition: all 0.2s linear;
  cursor: pointer;
}
.modal__inner-item:hover {
