.tab-header {
  padding: 10px 15px;
  height: 40px;
  text-align: center;
  cursor: pointer;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #787878;
  transition: color 0.2s linear;
}
.tab-header_show {
  color: #fff;
  background: linear-gradient(90deg, #F78DD0 0%, #AD38EF 100%);
  border-radius: 10px;
}
.tab-header:hover {
  color: #fff;
}

.tab-content {
  display: none;
  text-align: center;
}
.tab-content_show {
  display: block;
}

.fade {
  -webkit-animation: tabContent 0.5s cubic-bezier(0.6, 0, 0.18, 0.99);
          animation: tabContent 0.5s cubic-bezier(0.6, 0, 0.18, 0.99);
}

@-webkit-keyframes tabContent {
  0% {
    opacity: 0;
    transform: scale(0.98);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes tabContent {
  0% {
    opacity: 0;
    transform: scale(0.98);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
